h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  font-weight: normal;
  font-style: normal;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #c0e3da;
}

#counter {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  letter-spacing: 0.04rem;
  padding: 0.5rem 2rem;
  border-radius: 0.4rem;
  border: 0.3rem solid #000;
  background: #fff;
  position: relative;
  box-shadow: 0 3px 0 0 #000;
  cursor: pointer;
  outline: none;
  margin: 30px 80px;
}

#counter:active {
  top: 3px;
  box-shadow: none;
}

.counterText {
  position: relative;
  font-size: 2.5rem;
  font-family: "Lato", sans-serif;
}
